.header-section {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 999;

	.container-fluid {
		padding: 0 275px;

		@include breakpoint (max-xl4) {
			padding: 0 20px;
		}
	}

	.header-top-3 {
		background-color: $theme-color;
	}
}

.header-section-2 {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 999;

	// @include breakpoint (max-xxxl){
	// 	position: static;
	// }

	.container-fluid {
		padding: 0 250px;

		@include breakpoint (max-xl4) {
			padding: 0 20px;
		}
	}

	@include breakpoint (max-xxl) {
		background-color: $white
	}

	&::before {
		@include before;
		background-color: $bg-color-2;
		z-index: -1;
		height: 56%;
	}

	&.style-two {
		&::before {
			display: none;
		}
	}
}

.header-top-wrapper {
	@include flex;
	justify-content: space-between;
	margin-bottom: 5px;

	@include breakpoint (max-lg) {
		display: none;
	}

	p {
		font-weight: 700;
		color: $black;

		a {
			color: $black;
		}
	}

	.header-top-right {
		@include flex;
		gap: 40px;

		.social-icon {
			gap: 20px;

			a {
				&:hover {
					color: $theme-color;
				}
			}
		}

		.css-13cymwt-control {
			border: none;
			background: transparent;

			.nice-select {
				padding: 10px 7px 10px 20px;
				background: transparent;
				border: none;
				text-align: center;
				margin: 0 auto;
				position: relative;
				z-index: 999;

				span {
					font-size: 16px;
					font-weight: 700;
					text-transform: uppercase;
					color: $header-color;
				}

				&::after {
					right: -10px;
					top: 24px;
				}

				.list {
					background-color: $bg-color;
					border-radius: 0;
					right: initial;
					font-size: 14px;
					//padding: 5px 10px;
					margin-top: 0;

					li {
						font-weight: 500;
					}
				}

				.option {
					border: none;
				}
			}
		}
	}

	&.style-3 {
		margin-bottom: 0;
		padding: 8px 0;

		p {
			color: $white;

			a {
				color: $white;
			}
		}

		.header-top-right {
			.social-icon {
				a {
					color: $white;

					&:hover {
						color: #FF003D;
					}
				}
			}

			.nice-items {
				.nice-select {
					color: $white;

					option {
						color: $black;
					}

				}
			}
		}
	}
}

.header-top-wrapper-2 {
	padding: 18px 30px 0;
	@include flex;
	justify-content: space-between;
	background-color: $white;
	position: relative;

	@include breakpoint (max-xl) {
		display: none;
	}

	.coming-soon {
		@include flex;
		gap: 24px;

		ul {
			@include flex;
			gap: 20px;

			li {
				font-size: 18px;
				font-weight: 700;
				color: $theme-color;
			}
		}

		.theme-btn {
			border-radius: 9px;
			background-color: $theme-color;
			padding: 16.5px 22px;
			min-width: 140px;

			&::before {
				background-color: $header-color;
			}
		}
	}

	h6 {
		font-family: $body-font;

		i {
			color: $theme-color;
		}
	}

	.header-top-right-2 {
		@include flex;
		gap: 35px;

		.social-icon {
			@include flex;
			gap: 20px;

			a {
				color: $text-color;

				&:hover {
					color: $theme-color;
				}
			}
		}

		.flag-wrap {
			position: relative;
			width: 150px;
			background-color: #E8EAEF;
			border-radius: 35px;

			.nice-select {
				padding: 12px 7px 10px 5px;
				background: transparent;
				border: none;
				text-align: center;
				margin: 0 auto;
				position: relative;
				z-index: 999;

				span {
					font-size: 16px;
					font-weight: 700;
					text-transform: capitalize;
					color: $header-color;
				}

				&::after {
					height: 8px;
					width: 8px;
					right: 25px;
					top: 27px;
				}

				.list {
					right: 0;
					background-color: $white;
				}

				.option {
					border: none;
					padding: 4px 5px;
				}

				&:focus-visible {
					outline: 0;
				}
			}

			.flag {
				position: absolute;
				top: 9px;
				left: 15px;
				z-index: 1;

				@include breakpoint(max-md) {
					display: none
				}

				img {
					@include imgw;
				}
			}
		}
	}
}


.menu-thumb {
	@include breakpoint (max-xl) {
		display: none !important;
	}
}

.header-main {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10px 0;

	.main-menu {
		@media (max-width:991px) {
			display: none;
		}

		ul {
			margin-bottom: 0;

			li {
				position: relative;
				list-style: none;
				display: inline-block;
				margin-inline-end: 50px;

				&:last-child {
					margin-inline-end: 0;
				}

				a {
					display: inline-block;
					font-size: 16px;
					font-weight: 600;
					color: $header-color;
					padding: 20px 0;
					text-align: left;
					position: relative;
					text-transform: capitalize;
					font-weight: 700;
					@include transition;

					i {
						margin-left: 4px;
					}

					&:hover {
						color: $theme-color !important;
					}

				}

				.submenu {
					position: absolute;
					top: 115%;
					inset-inline-start: 0;
					min-width: 240px;
					background: $white;
					padding: 20px 0;
					z-index: 99999;
					visibility: hidden;
					opacity: 0;
					transform-origin: top center;
					color: $header-color;
					box-shadow: rgba(149, 157, 165, 0.1) 0px 8px 24px;
					transform: translateY(-10px);
					@include transition;
					border-top: 6px solid $theme-color;

					li {
						display: block;
						width: 100%;
						margin: 0;


						a {
							position: relative;
							z-index: 11;
							font-size: 16px;
							font-weight: 700;
							color: $header-color;
							line-height: 38px;
							padding: 0px 0px 0px 32px;
							width: 100%;

							&::before {
								content: "";
								position: absolute;
								width: 0px;
								height: 2px;
								background: $theme-color;
								left: 14px;
								bottom: 18px;
								transition: all 0.4s ease-in-out;
							}

							&:hover {
								color: $white !important;
							}
						}

						&:last-child {
							a {
								border: none;
							}
						}

						.submenu {
							inset-inline-start: 100%;
							top: 0;
							visibility: hidden;
							opacity: 0;
						}

						&:hover {
							>a {
								color: $theme-color !important;
								margin-left: 10px;

								&::before {
									width: 10px;
								}

								&::after {
									color: $theme-color;
								}
							}

							>.submenu {
								-webkit-transform: translateY(1);
								-moz-transform: translateY(1);
								-ms-transform: translateY(1);
								-o-transform: translateY(1);
								transform: translateY(1);
								visibility: visible;
								opacity: 1;
							}
						}
					}

					li.has-dropdown {
						>a {
							&::after {
								position: absolute;
								top: 50%;
								inset-inline-end: 25px;
								-webkit-transform: translateY(-50%);
								-moz-transform: translateY(-50%);
								-ms-transform: translateY(-50%);
								-o-transform: translateY(-50%);
								transform: translateY(-50%);
								color: $theme-color;
							}
						}
					}
				}

				.has-homemenu {
					width: 800px;
					padding: 30px 30px 10px 30px;
					opacity: 0;
					left: -250px;
					visibility: hidden;
					padding: 30px 30px 10px 30px;

					.homemenu-items {
						@include flex;
						gap: 30px;
						justify-content: space-between;

						@include breakpoint (max-lg) {
							flex-wrap: wrap;
						}

						.homemenu {
							position: relative;

							.homemenu-thumb {
								position: relative;

								.demo-button {
									position: absolute;
									top: 50%;
									left: 50%;
									transform: translate(-50%, -50%);
									width: 70%;
									gap: 10px;
									display: flex;
									justify-content: center;
									flex-direction: column;
									opacity: 0;
									visibility: hidden;
									@include transition;
									margin-top: 20px;

									.theme-btn {
										padding: 14px 20px;
										color: $white !important;
										width: initial;
										font-size: 16px;
										text-align: center;
										border-radius: 0px !important;
										background-color: $theme-color;
										line-height: initial;

										&:hover {
											color: $white !important;
											background-color: $header-color !important;

											&::before {
												background-color: $header-color !important;
											}
										}
									}
								}

								&::before {
									background: -webkit-gradient(linear, left top, left bottom, from(rgba(20, 19, 19, 0)), to(#5e5ef6));
									background: linear-gradient(to bottom, rgba(99, 92, 92, 0) 0%, #252527 100%);
									background-repeat: no-repeat;
									background-size: cover;
									background-position: center;
									width: 100%;
									height: 100%;
									position: absolute;
									left: 0;
									top: 0;
									overflow: hidden;
									opacity: 0;
									-webkit-transition: all 0.3s ease-in-out;
									transition: all 0.3s ease-in-out;
									content: "";
								}

								&:hover {

									&::before {
										visibility: visible;
										opacity: 1;
									}

									.demo-button {
										opacity: 1;
										visibility: visible;
										margin-top: 0;
									}

									& .homemenu-btn {
										opacity: 1;
										visibility: visible;
										bottom: 50%;
										transform: translateY(50%);
									}
								}

								img {
									width: 100%;
								}
							}

							.homemenu-title {
								text-align: center;
								margin: 15px auto;
								display: inline-block;
								font-size: 16px;
							}
						}
					}
				}

				&:hover {
					>a {
						color: $theme-color;

						&::after {
							color: $theme-color;
						}
					}

					>.submenu {
						visibility: visible;
						opacity: 1;
						transform: translateY(0px);
					}
				}
			}
		}
	}

	.header-right {
		gap: 40px;

		@include breakpoint (max-xxl) {
			gap: 20px;
		}

		button {
			color: var(--header);
			transition: all 0.4s ease-in-out;
			font-weight: 700;
			font-family: $body-font;

			span {
				display: inline-block;
				width: 16px;
				height: 16px;
				line-height: 16px;
				border-radius: 50%;
				background-color: var(--header);
				color: var(--white);
				text-align: center;
				font-size: 12px;
			}
		}

		.mobail-menu button {
			background-color: #0D1224;
			border: 0;
			height: 35px;
			outline: 0;
			padding: 9px 10px;
			position: relative;
			width: 40px;
			z-index: 20;
		}

		.mobail-menu button span {
			background-color: #fff;
			display: block;
			height: 2px;
			margin-bottom: 5px;
			width: 20px;
		}
	}

	.sidebar__toggle {
		cursor: pointer;
		font-size: 20px;
	}
}

