//>>>>> Sticky Start <<<<</
.sticky {
	position: fixed !important;
	top: 0 !important;
	left: 0;
	width: 100%;
	z-index: 100;
	transition: all 0.9s;
	background-color: $white;
	-webkit-animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
	animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
	box-shadow: $shadow;

	&.header-1 {
		.mega-menu-wrapper {
			padding: 0;
			border-radius: 0;

			&::before {
				right: 100px;
			}
		}
	}
}

//>>>>> Offcanvas Start <<<<<//
.offcanvas__info {
	background: $bg-color none repeat scroll 0 0;
	border-left: 2px solid $theme-color;
	position: fixed;
	right: 0;
	top: 0;
	width: 400px;
	height: 100%;
	-webkit-transform: translateX(calc(100% + 80px));
	-moz-transform: translateX(calc(100% + 80px));
	-ms-transform: translateX(calc(100% + 80px));
	-o-transform: translateX(calc(100% + 80px));
	transform: translateX(calc(100% + 80px));
	-webkit-transition: transform 0.45s ease-in-out, opacity 0.45s ease-in-out;
	-moz-transition: transform 0.45s ease-in-out, opacity 0.45s ease-in-out;
	transition: transform 0.45s ease-in-out, opacity 0.45s ease-in-out;
	z-index: 99999;
	overflow-y: scroll;
	overscroll-behavior-y: contain;
	scrollbar-width: none;

	&::-webkit-scrollbar {
		display: none;
	}
}

.offcanvas__info.info-open {
	opacity: 1;
	-webkit-transform: translateX(0);
	-moz-transform: translateX(0);
	-ms-transform: translateX(0);
	-o-transform: translateX(0);
	transform: translateX(0);
}

.offcanvas__logo {
	a {
		img {
			width: 150px;
		}
	}
}

.offcanvas__wrapper {
	position: relative;
	height: 100%;
	padding: 30px 30px;

	.offcanvas__content {
		.text {
			color: $text-color;
		}

		.offcanvas__close {
			width: 45px;
			height: 45px;
			line-height: 45px;
			text-align: center;
			border-radius: 50%;
			background-color: $theme-color;
			position: relative;
			z-index: 9;
			cursor: pointer;

			i {
				color: $white;
			}
		}

		.offcanvas__contact {
			margin-top: 20px;

			ul {
				margin-top: 20px;

				li {
					font-size: 16px;
					font-weight: 600;
					text-transform: capitalize;

					a {
						color: $text-color;
					}

					&:not(:last-child) {
						margin-bottom: 15px;
					}

					.offcanvas__contact-icon {
						margin-right: 20px;

						i {
							color: $theme-color;
						}
					}
				}
			}

			span {
				text-transform: initial;
			}

			.header-button {

				.theme-btn {
					width: 100%;
					padding: 16px 40px;
					text-transform: capitalize !important;
					color: $white;
					font-weight: 700;
					border-radius: 4px;
					background-color: $theme-color;

					i {
						margin-left: 10px;
					}

					&::before {
						background-color: $header-color;
					}

					&:hover {
						background-color: $white;
					}
				}
			}

			.social-icon {
				margin-top: 30px;
				gap: 10px;

				a {
					width: 45px;
					height: 45px;
					line-height: 45px;
					text-align: center;
					font-size: 16px;
					display: block;
					background: transparent;
					color: $text-color;
					border-radius: 50%;
					-webkit-transition: all .4s ease-in-out;
					transition: all .4s ease-in-out;
					text-align: center;
					border: 1px solid $border-color;

					&:hover {
						background-color: $theme-color;
						color: $white;
					}
				}
			}
		}
	}
}

.offcanvas__overlay {
	position: fixed;
	height: 100%;
	width: 100%;
	background: #151515;
	z-index: 900;
	top: 0;
	opacity: 0;
	visibility: hidden;
	right: 0;
}

.offcanvas__overlay.overlay-open {
	opacity: 0.8;
	visibility: visible;
}

@media (max-width:450px) {
	.offcanvas__info {
		width: 300px;
	}
}

@media (max-width: 575px) {
	.offcanvas__wrapper {
		padding: 20px;
	}
}


//>>>>> Breadcrumb Start <<<<<//
.breadcrumb-section {
	padding-top: 88px;

	@include breakpoint (max-xxxl) {
		padding-top: 0;
	}

	.container-fluid {
		padding: 0 68px;

		@include breakpoint (max-xl4) {
			padding: 0 20px;
		}
	}
}

.breadcrumb-wrapper {
	position: relative;
	overflow: hidden;
	border-radius: 25px;

	@include breakpoint (max-xl4) {
		border-radius: 0;
	}

	.flower-shape {
		position: absolute;
		top: 90px;
		left: 40px;
		animation: translateY2 2s forwards infinite alternate;

		@include breakpoint (max-md) {
			display: none;
		}
	}

	.star-shape {
		position: absolute;
		right: 80px;
		bottom: 70px;
		animation: translateX2 2s forwards infinite alternate;

		@include breakpoint (max-md) {
			display: none;
		}
	}

	.page-heading {
		position: relative;
		padding: 200px 0 130px;
		z-index: 9;

		@include breakpoint (max-xxxl) {
			padding-top: 250px;
		}

		@include breakpoint (max-xl) {
			padding-top: 190px;
		}

		@include breakpoint (max-md) {
			padding: 170px 0 100px;
		}

		@include breakpoint (max-sm) {
			padding: 150px 0 80px;
		}

		h6 {
			font-size: 18px;
			font-weight: 500;
			font-family: $heading-font;
			background-color: $theme-color;
			color: $white;
			padding: 14px 30px;
			border-radius: 50px;
			display: inline-block;
			margin-bottom: 25px;
			line-height: 1;

			@include breakpoint (max-sm) {
				font-size: 16px;
				padding: 14px 20px;
			}
		}

		h1 {
			color: $header-color;
			font-size: 80px;
			position: relative;
			z-index: 9;
			font-weight: 800;

			@include breakpoint(max-lg) {
				font-size: 68px;
			}

			@include breakpoint(max-sm) {
				font-size: 45px;
			}
		}
	}
}


.error-content {
	margin-top: -58px;

	@include breakpoint (max-xl) {
		margin-top: -42px;
	}

	@include breakpoint (max-lg) {
		margin-top: -30px;
	}

	@include breakpoint (max-sm) {
		margin-top: -18px;
	}

	h2 {
		font-weight: 700;
		font-size: 400px;
		line-height: 1;

		@include breakpoint (max-xl) {
			font-size: 300px;
		}

		@include breakpoint (max-lg) {
			font-size: 200px;
		}

		@include breakpoint (max-sm) {
			font-size: 110px;
		}
	}

	h3 {
		font-weight: 700;
		font-size: 55px;
		text-transform: uppercase;

		@include breakpoint (max-lg) {
			font-size: 48px;
		}

		@include breakpoint (max-sm) {
			font-size: 36px;
		}

		@include breakpoint (max-sm) {
			font-size: 36px;
		}
	}

	.theme-btn {
		border-radius: 9px;
		background-color: $theme-color;

		&::before {
			background-color: $header-color;
		}
	}
}